body, p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

ul, ol {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

button {
  cursor: pointer;
}

a {
  text-decoration: none;
}

.no-scroll {
  overflow: hidden;
}

.container {
  margin: 0 auto;
  padding: 0 16px;
}

.search-form {
  width: 100vw;
  background-color: #315da7;
  justify-content: center;
  align-items: center;
  padding: 10px;
  display: flex;
  box-shadow: 0 2px 7px #315da7;
}

.search-form input {
  width: 300px;
  height: 26px;
  border: 3px solid #add8e6;
  border-right: none;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  padding: 5px;
  font-size: 14px;
}

.search-form > button {
  height: 42px;
  width: 36px;
  border: 3px solid #add8e6;
  border-left: none;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}

.search-form > button:hover, .search-form > button:focus {
  background-color: #d3d3d3;
}

#loadMore {
  min-width: 100px;
  color: #fff;
  background-color: #315da7;
  border: none;
  border-radius: 5px;
  margin: 0 auto 20px;
  padding: 10px;
  display: block;
}

.gallery {
  flex-wrap: wrap;
  gap: 20px;
  padding: 20px 0;
  display: flex;
}

.photo-card {
  width: calc(25% - 15px);
  border-radius: 4px;
  overflow: hidden;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.photo-card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.photo-link {
  width: 100%;
  height: 200px;
  cursor: pointer;
}

.info {
  text-align: center;
  background-color: #f5f4fa;
  justify-content: center;
  align-items: center;
  padding: 10px;
  display: flex;
}

#loadMore.hidden {
  display: none;
}

#loadMore[disabled] {
  background-color: gray;
}

